
/* PC , 테블릿 가로 (해상도1024px ~)*/ 
@media screen and (min-width: 1024px) 
{
    .common-wrapper{
        display: grid;
        gap: 0px 0px;
        grid-template-columns: auto 1000px auto;
        grid-template-rows: auto auto;       
        box-sizing: border-box; 
        /* background-color: rgb(107, 149, 197) ; */
    }
    .common-grid--col2__11 {
      display: grid;
      gap: 10px 10px;
      grid-template-columns: 1fr 1fr ;
      grid-template-rows: auto;
      /* background-color: rgb(107, 149, 197) ; */
    }
    .common-grid--col3__111 {
        display: grid;
        gap: 10px 10px;
        grid-template-columns: 1fr 1fr 1fr ;
        grid-template-rows: auto;
        /* background-color: rgb(107, 149, 197) ; */
      }
      .common-grid--col4__1111 {
        display: grid;
        gap: 10px 10px;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: auto;
        /* background-color: rgb(107, 149, 197) ; */
      }
    .common-grid--col3__21 {
        display: grid;
        gap: 0px 0px;
        grid-template-columns: 2fr 1fr ;
        grid-template-rows: auto;
        /* background-color: rgb(107, 149, 197) ; */
      }
      .common-grid--col3__12 {
        display: grid;
        gap: 10px 10px;
        grid-template-columns: 1fr 2fr ;
        grid-template-rows: auto;
        /* background-color: rgb(107, 149, 197) ; */
      }

      .span-good-load{
        font-family: Fredoka One;
        font-style: normal;
        font-weight: normal;
        font-size: 120px;
        line-height: 145px;
        letter-spacing: 0.01em;
        color: #FFB300;
        padding-top:40px;
    }  
    .span-group-search{
        height: 84px;
        font-family: "Sandoll Samliphopangche";
        font-style: normal;
        font-weight: 550;
        font-size: 38px;
        line-height: 42px;  
        text-align: center;
        letter-spacing: 0.03em;
        color: #573300;
        padding-top: 10px;
    }  
    .icon-quote{
        width: 28px;
        height: 24px; 
    }
    .common-wrapper__footer{
        padding-left: 10px;
        padding-right: 10px;
        background-color: #F8F8F8;
    }
    /* 공백 */
    .common-blank-60{
        height: 60px;
    }
    /* 공백 */
     .common-blank-50{
        height: 50px;
    }

/* 로그인 */
    .sign-container{
        width: 548px;
        min-height: 600px;
        background: #FFFFFF;
        border: 2px solid #FFBF26;
        box-sizing: border-box;
    }
    .sign-bottom-box{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin-top: 60px;
        width: 546px;
        height: 137px;
        background: #FFBF26;
        box-sizing: border-box;
    }
    .group-info-container{
        background: #FFF9E3;
        border-radius: 21px;
        padding:40px;
      }

      /* 흰색 라운드 박스 */
 .common-container--round-white{
