 /* 모바일 가로, 테블릿 세로 (해상도 ~ 479px)*/ 
 @media all and (max-width:767px) 
{
   .common-wrapper {
       display: grid;
       gap: 0px 0px;
       grid-template-columns: 1fr;
       grid-template-rows: auto auto;
     }
     .common-grid--col2__11 {
      display: grid;
      gap: 10px 10px;
      grid-template-columns: 1fr ;
      grid-template-rows: auto;
      /* background-color: rgb(107, 149, 197) ; */
    }
   .common-grid--col3__111 {
       display: grid;
       gap: 20px 20px;
       grid-template-columns: 1fr;
       grid-template-rows: auto;
       /* background-color: rgb(107, 149, 197) ; */
   }
   .common-grid--col4__1111 {
    display: grid;
    gap: 10px 10px;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    /* background-color: rgb(107, 149, 197) ; */
  }
   .common-grid--col3__21 {
       display: grid;
       gap: 0px 0px;
       grid-template-columns: 1fr;
       grid-template-rows: auto;
       /* background-color: rgb(107, 149, 197) ; */
     }  
     .common-grid--col3__12 {
      display: grid;
      gap: 10px 10px;
      grid-template-columns: 1fr ;
      grid-template-rows: auto;
      /* background-color: rgb(107, 149, 197) ; */
    }
     .span-good-load{
        font-family: Fredoka One;
        font-style: normal;
        font-weight: normal;
        font-size: 40px;
        line-height: 60px;
        letter-spacing: 0.01em;
        color: #FFB300;
    }  
    .span-group-search{
        font-family: "Sandoll Samliphopangche";
        font-style: normal;
        font-weight: 550;
        font-size: 26px;
        line-height: 22px;
        text-align: center;
        letter-spacing: 0.03em;
        color: #573300;
        padding-top: 10px;
    }  
    .icon-quote{
        width: 16px;
        height: 12px;
    }
    .common-wrapper__footer{
        padding-left: 10px;
        padding-right: 10px;
        background-color: #F8F8F8;
    }

    /* 공백 */
    .common-blank-60{
        height: 10px;
    }
    /* 공백 */
     .common-blank-50{
        height: 10px;
    }


    /* 로그인 */
    .sign-container{
        width: 100%;
        min-height: 500px;
        background: #FFFFFF;
        border: 2px solid #FFBF26;
        box-sizing: border-box;
    }
    .sign-bottom-box{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin-top: 50px;
        width: 100%;
        height: 137px;
        background: #FFBF26;
        box-sizing: border-box;
    }
    .group-info-container{
        background: #FFF9E3;
        border-radius: 21px;
        padding:10px;
      }

         /* 흰색 라운드 박스 */
 .common-container--round-white{
