html {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.common-bg{
  background-image: url( "../images/main/bg-image2.png" ), url( "../images/main/bg-image.png" );
  background-size:1920px 1371px, 1920px 815px; 
  background-position:0px 900px ,0 0;
  background-repeat:no-repeat;
}

/* wrapper inner */
.common-wrapper__header{
  margin: 0px; 
}
.common-wrapper__container{
  margin: 0px; 
}


/* grid 내그룹 111*/
.common-grid--col3__111 .box-cgc{
  padding: 0px;  
  /* background-color: rgb(90, 47, 189) ; */
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.common-grid--col3__111 .box-cgc-padding{
  padding: 4px;  
  /* background-color: rgb(90, 47, 189) ; */
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}


/* grid 내그룹 21*/
.common-grid--col3__21 .box-cgc{
  padding: 8px; 
  /* background-color: rgb(90, 47, 189) ; */
}
.common-grid--col3__12 .box-cgc{
  padding: 8px; 
  /* background-color: rgb(90, 47, 189) ; */
}




/* flex row 가로방향 */
.common-flex-row-wrap-vcenter{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}
.common-flex-row-nowrap-vcenter{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}
.common-flex-row-reverse-wrap-vcenter{
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  align-items: center;
}
.common-flex-row-wrap-flex-start{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
}


.common-flex-row-wrap-space-between-vcenter{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.common-flex-row-wrap-space-between-vstretch{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
}

.common-flex-row-wrap-hcenter-vcenter{
display: flex;
flex-direction: row;
flex-wrap: wrap;
justify-content: center;
align-items: center;
}
.common-flex-row-wrap-hcenter-vcenter .item-lr-50pro{
  min-width:50%;
}


.common-row-wrap-hcenter-vstretch{
display: flex;
flex-direction: row;
flex-wrap: wrap;
justify-content: center;
align-items: stretch;
}
.common-row-wrap-vstretch{
display: flex;
flex-direction: row;
flex-wrap: wrap;
align-items: stretch;
}


/* flex column 세로방향 */
.common-flex-column-wrap{
display: flex;
flex-direction: column;
flex-wrap: wrap;
}
.common-flex-column-wrap-hcenter-vcenter{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
flex-wrap: wrap;
}







/* 인기그룹 CardViewSmall 리스트 외곽뷰 */
.common-flex-column-wrap .item-basci{
border-radius: 13px;
background: #FFFFFF;
box-shadow: 0px -10px 20px rgba(0, 0, 0, 0.04);
border-radius: 13px;
margin-bottom: 10px;
}
/* 인기그룹 / 추천치료사 타이틀 */
.common-flex-column-wrap .item-title-basci{
height: 42px;
}






.slider-content-wrapper {
  width: 40%;
  margin: 0 auto;
  min-width: 185px;
}

.slider-userselect {
  -webkit-user-select: none;
  /* Safari 3.1+ */
  -moz-user-select: none;
  /* Firefox 2+ */
  -ms-user-select: none;
  /* IE 10+ */
  user-select: none;
  /* Standard syntax */
}

.labelText {
  text-align: left;
  font-weight: 500;
  padding-bottom: 10px;
}

.slider_container {
  margin-top: 40px;
}

.e-bigger .content-wrapper {
  width: 80%;
}

#square_slider .e-handle {
  border-radius: 0;
  background-color: #f9920b;
  border: 0;
}

#circle_slider .e-handle {
  background-color: #f9920b;
  border-radius: 50%;
  border: 0;
}

#image_slider .e-handle {
  height: 25px;
  width: 24px;
  background-size: 24px;

}
.rc-slider-handle-l{
  height: 30px;
  width: 34px;
  background-size: 34px;
  background-image: url('../images/main/slider-left.svg');
  background-repeat: no-repeat;
  background-color: transparent;
  border: 0;
} 
.rc-slider-handle-r{
  height: 30px;
  width: 33px;
  background-size: 33px;
  background-image: url('../images/main/slider-right.svg');
  background-repeat: no-repeat;
  background-color: transparent;
  border: 0;
}

#image_slider .e-handle {
  background-image: url('../images/main/slider-left.svg');
  background-repeat: no-repeat;
  background-color: transparent;
  border: 0;
}

#square_slider .e-tab-handle::after,
#circle_slider .e-tab-handle::after {
  background-color: #f9920b;
}

#image_slider .e-tab-handle::after {
  background-color: transparent;
}

#oval_slider .e-handle {
  height: 25px;
  width: 8px;
  top: 3px;
  border-radius: 15px;
  background-color: #f9920b;
}
