/* 테블릿 세로 (해상도 768px ~ 1023px)*/
@media all and (min-width:768px) and (max-width:1023px) 
{ 
   .common-wrapper {
       display: grid;
       gap: 0px 0px;
       grid-template-columns: 1fr;
       grid-template-rows: auto auto;
     }
     .common-grid--col2__11 {
        display: grid;
        gap: 10px 10px;
        grid-template-columns: 1fr 1fr ;
        grid-template-rows: auto;
        /* background-color: rgb(107, 149, 197) ; */
      }
   .common-grid--col3__111 {
       display: grid;
       gap: 0px 0px;
       grid-template-columns: 1fr 1fr ;
       grid-template-rows: auto;
       /* background-color: rgb(107, 149, 197) ; */
   }
   .common-grid--col4__1111 {
    display: grid;
    gap: 10px 10px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    /* background-color: rgb(107, 149, 197) ; */
  }
   .common-grid--col3__21 {
       display: grid;
       gap: 0px 0px;
       grid-template-columns: 1fr;
       grid-template-rows: auto;
       /* background-color: rgb(107, 149, 197) ; */
     }  
     .common-grid--col3__12 {
      display: grid;
      gap: 10px 10px;
      grid-template-columns: 1fr ;
      grid-template-rows: auto;
      /* background-color: rgb(107, 149, 197) ; */
    }
     .span-good-load{
        font-family: Fredoka One;
        font-style: normal;
        font-weight: normal;
        font-size: 80px;
        line-height: 145px;
        letter-spacing: 0.01em;
        color: #FFB300;
    }  
    .span-group-search{
        font-family: "Sandoll Samliphopangche";
        font-style: normal;
        font-weight: 550;
        font-size: 28px;
        line-height: 32px;
        text-align: center;
        letter-spacing: 0.03em;
        color: #573300;
        padding-top: 10px;
    }  
    .icon-quote{
        width: 18px;
        height: 14px;
    }
    .common-wrapper__footer{
        padding-left: 10px;
        padding-right: 10px;
        background-color: #F8F8F8;
    }
    /* 공백 */
    .common-blank-60{
        height: 20px;
    }
    /* 공백 */
     .common-blank-50{
        height: 20px;
    }

    /* 로그인 */
    .sign-container{
        width: 548px;
        min-height: 550px;
        background: #FFFFFF;
        border: 2px solid #FFBF26;
        box-sizing: border-box;
    }
    .sign-bottom-box{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin-top: 80px;
        width: 546px;
        height: 137px;
        background: #FFBF26;
        box-sizing: border-box;
    }
    .group-info-container{
        background: #FFF9E3;
        border-radius: 21px;
        padding:20px;
      }
         /* 흰색 라운드 박스 */
 .common-container--round-white{
    background: #FFFFFF;
    border-radius: 21px;
    padding:20px;
  }
  .common-container-round-white-shadow{
    flex-grow: 1;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 28px;
    padding:20px;
  }
  .common-container-round-white-shadow-padding20{
    flex-grow: 1;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 28px;
    padding:10px;
  }
  .common-container-round-white-shadow-padding10{
    flex-grow: 1;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 28px;
    padding:10px;
  }
  .common-container-round-grey-shadow{
    flex-grow: 1;
    background: #F6F4F2;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 28px;
    padding:20px;
  }
     
} 


